@import url(https://fonts.googleapis.com/css?family=Roboto:400,700&display=swap);
.login-wrap {
  background: rgba(15, 15, 15, 0.7);
  width: 100vw;
  height: 100vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center; }

.login {
  border-radius: 3px;
  width: 30%;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  color: var(--white); }
  .login__header {
    font-size: 40px;
    margin: 0px; }
    .login__header span {
      color: var(--main);
      font-style: italic; }
  .login__subheader {
    margin-bottom: 60px; }

:root {
  --main: #4dc822;
  --dark: #0f0f0f;
  --focus: #ff9901;
  --light: #313131;
  --grey: #bdbdbd;
  --white: #fff; }

html, body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif; }

.logout {
  color: #ff9901;
  color: var(--focus); }

.blog-footer {
  margin-top: auto;
  padding: 20px;
  text-align: center;
  color: white;
  border-top: 1px solid grey; }
  .blog-footer p {
    margin: 0; }

.popup-style {
  text-align: center; }
  .popup-style p {
    margin: 0; }
  .popup-style .popup-label {
    color: #bdbdbd;
    color: var(--grey);
    margin-top: 10px; }
  .popup-style .popup-footer {
    display: flex;
    justify-content: space-around;
    padding: 10px 0px;
    margin-top: 10px;
    border-top: 1px solid grey; }

